import React from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import './HomePage.scss';

function HomePage() {
  return (
    <Container className="page home">
      <Row className="about-us">
        <Col md={12} className="section-heading text-center">
          <h2>À propos de nous</h2>
          <h3>Passionné par le fait main, je concoit des objets pour la maison, souvent fabriqués à l'aide d'une imprimante 3D.</h3>
        </Col>
      </Row>
      <Row>
        <Col md={8}>
          <Image src="img/intro.jpg" thumbnail alt="Support Philips Pasta Maker Avance 32 filière" />
        </Col>
        <Col md={4}>
          <h2>Les supports pour filières</h2>
          <p>Notre plus grand succès aujourd'hui: un support pour filières pour machines à pates.</p>
          <p>Il est disponible pour les machines <strong>Philips Pasta Maker Avance</strong> et <strong>Philips Pasta Maker Viva</strong>, en plusieurs capacités et en plusieurs couleurs.</p>
          <p>De nouveaux modèles sont en préparations, restez à l'écoute !</p>
        </Col>
      </Row>
    </Container>
  )
}

export default HomePage;
